
import React, { useRef, useState } from 'react'
import ReactModal from 'react-modal';

const SingUp = ({isOpen}) => {
    const [subEmail, setSub] = useState();
    const subEmailEl = useRef();
    const [tell, setTell] = useState();
    const [name, setName] = useState();
    const [id, setId] = useState();
    const [pass, setPass] = useState();
    const [psMessage, setPsMessage] = useState('');
    const [tellMessage, setTellMessage] = useState('');
    const handleSubmit = (e) => {
        e.preventDefault();
    }
    
    function changeMail(e) {

        var sub = e.target.value;
        setSub(sub);
        if (sub != 'defalut') {
            subEmailEl.current.value = sub;
        }
        else {
            subEmailEl.current.value = '';
        }
        /* setSub */
    }
    function checkPass(e) {
        pass == e.target.value ? setPsMessage(' .') : setPsMessage('  .');
    }
    function checkTell(e) {
        var currentPhone = e.target.value;
        console.log();
        setTell(currentPhone);
        var currentPhoneCk = /[-]/;
        if (currentPhone.length == 11 && currentPhoneCk.test(currentPhone)) {
            setTellMessage('')
        }
        else {
            setTellMessage('  .')
        }
    }

    return (
        <ReactModal isOpen={isOpen}>
            <div className='SingUp-back'>
                <section className='SingUp-container'>
                    <div className='form-box'>
                        <p></p>
                        <form onSubmit={handleSubmit} >
                            <div className='id_box'>
                                <input type="text" placeholder=' .' />
                                <button className='but'></button>
                            </div>
                            <input type="password" onChange={(e) => setPass(e.target.value)} placeholder=" " />

                            <input type="password" onChange={checkPass} placeholder=" " />
                            <p style={{ color: 'red' }}>{psMessage}</p>
                            <input type="text" onChange={(e) => setName(e.target.value)} placeholder="" />

                            <input type="text" onChange={checkTell} placeholder=" ('-'  11)" />
                            <p style={{ color: 'red' }}>{tellMessage}</p>
                            <div className='email-box'>
                                <input type="text" placeholder="  " />
                                <p>@</p>
                                <input type="text" disabled={subEmail == 'defalut' ? false : true} ref={subEmailEl} />
                                <select onChange={changeMail}>
                                    <option value='defalut'></option>
                                    <option value='naver.com'>naver.com</option>
                                    <option value='gmail.com'>gmail.com</option>
                                </select>
                            </div>
                            <div className='btn-box'>
                                <button type='submit' value=''></button>
                                <button ></button>
                            </div>
                        </form>
                    </div>
                </section>

            </div>
            </ReactModal>
    )
}

export default SingUp