import React, { useContext, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import { AnimalContext } from '../Context';

function YugiInfo() {
  const { id } = useParams();
  const { yugi } = useContext(AnimalContext);
  const [yugiInfo, setYugiInfo] = useState();
  var value;

  yugi && yugi.map((obj, key) => {
    if (obj.desertionNo == id) {
      value = obj;
    }
  });

  useEffect(() => {
    setYugiInfo(value);
  }, []);


 return(
  yugiInfo&&
  <section className='info-container'>
            <p> </p>
            <div className='info-content'>
                <div className='img-box'>
                    <img src={yugiInfo.popfile} alt=' '></img>

                </div>
                <div className='text-box'>
                    <ul>
                        <li> : {yugiInfo.desertionNo}</li>
                        <li> : {yugiInfo.noticeNo}</li>
                        <li> : {yugiInfo.happenDt}</li>
                        <li> : </li>
                        <li> : </li>


                    </ul>
                </div>

            </div>
            </section>
 )


}

export default YugiInfo